<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered modal-lg">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ (isAdd && (!columnId || columnId == 0)) ? $t('Add Invoice') : $t('Edit Invoice') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                role="button" aria-expanded="false" aria-controls="collapseExample">
                                <em class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}
                            </a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('User_Guide_Edit_Line_Item')"></div>
                                </div>
                            </div>
                        </span>
                    </div>

                    <div class="modal-body">
                        <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit" :mandatorytext="$t('MandatoryString')">

                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import { locateBillingService } from "../../../services/locateBillingService";
import commonMixin from '../../../assets/common/vue-common.js';
export default {
    mixins: [commonMixin],
    props: {
        clientId: {
            type: [Number, String],
            default: null
        },
        billId: {
            type: [Number, String],
            default: null
        },
        lineItemId: {
            type: [Number, String],
            default: null
        },
        columnId: {
            type: [Number, String],
            default: null
        },
        ticketType: {
            type: [Number, String],
            default: null
        },
        isTicketManual: {
            type: [String],
            default: null
        },
        batchId: {
            type: [String],
            default: null
        },
        divisionId: {
            type: [Number, String],
            default: null
        },
        dateOfInvoice: {
            type: [String, Date],
            default: null
        },
        lineBatchId: {
            type: [String],
            default: null
        },
        isAdd: {
            type: Boolean,
            default: false
        }
    },

    data() {
        return {
            isLoading: false,
            UtilityId: '',
            ActivityId: '',
            SbaId: '',
            oldValues: {},
            IsTicketMannual: this.isTicketManual || 'T',
            AdderUnit: 0,
            FormSchema: [{
                layoutType: "triple",
                Data: [                {
                    astype: "RadioField",
                    label: '',
                    name: "customlineitem",
                    value: this.IsTicketMannual,
                    visibility: this.isAdd && (this.columnId == 0 || this.columnId == null),
                    config: {
                        options: [
                            { name: this.$t('TicketNumber'), value: 'T' },
                            { name: this.$t('Manual'), value: 'M' }
                        ]
                    },
                    onChange: this.onCustomLineItemChange
                },
                {
                    astype: "TextField",
                    label: this.$t('TicketNumber'),
                    name: "TicketNumber",
                    value: "",
                    visibility: true,
                    config: {
                        onChange: this.onTicketNumberChange
                    },
                    validationRules: {
                        required: true,
                        max: 500
                    },
                    disabled: this.columnId > 0 || this.IsTicketMannual === 'M'
                },
                {
                    astype: "SelectField",
                    label: this.$t('ActivityId'),
                    name: "ActivityId",
                    value: "",
                    visibility: true,
                    config: {
                        options: [],
                        onChange: this.onActivityIdChange
                    },
                    validationRules: {
                        required: true
                    },
                    disabled: this.columnId > 0 || this.IsTicketMannual === 'M'
                },
                {
                    astype: "TextAreaField",
                    label: this.$t('Description'),
                    name: "Description",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    config: {
                        rows: 3
                    },
                    disabled: true
                },
                {
                    astype: "SelectField",
                    label: this.$t('StatusCode'),
                    name: "StatusCode",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    config: {
                        options: []
                    },
                    validationRules: {
                        required: true
                    },
                    disabled: this.IsTicketMannual === 'M'
                },
                {
                    astype: "SearchableSelectField",
                    label: this.$t('ContractName'),
                    name: "ClientId",
                    value: "",
                    visibility: true,
                    placeholder: "Select Client Name",
                    isSearch: false,
                    config: {
                        options: [],
                        actionButtons: { selectAll: true, active: true, inactive: true, clear: true },
                        groupFields: ['groupText'],
                        isMultiple: false,
                        minSearchLength: 0,
                        statusFieldConfig: {
                            fieldName: 'status',
                            activeValues: ['Active', 'active'],
                            inactiveValues: ['Inactive', 'inactive'],
                            activeColor: '#28a745',
                            inactiveColor: '#dc3545'
                        },
                        onChange: this.onSelectionChange
                    },
                    validationRules: {
                        required: false
                    },
                    disabled: false
                },
                {
                    astype: "TextField",
                    label: this.$t('TicketId'),
                    name: "TicketId",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('LocatorName'),
                    name: "LocatorName",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('StreetName'),
                    name: "StreetName",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('FeedName'),
                    name: "FeedName",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('TransmissionId'),
                    name: "TransmissionId",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "SelectField",
                    label: this.$t('TicketType'),
                    name: "TicketType",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    config: {
                        options: []
                    },
                    disabled: this.IsTicketMannual === 'M'
                },
                {
                    astype: "TextField",
                    label: this.$t('WorkArea'),
                    name: "WorkArea",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('CallDate'),
                    name: "CallDate",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('TransmitDate'),
                    name: "TransmitDate",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('LocatorStartDate'),
                    name: "LocatorStartDate",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('ParseDate'),
                    name: "ParseDate",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('County'),
                    name: "County",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('State'),
                    name: "State",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('CallerName'),
                    name: "CallerName",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('CallerPhone'),
                    name: "CallerPhone",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('WorkDoneFor'),
                    name: "WorkDoneFor",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('Excavator'),
                    name: "Excavator",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('TypeofWork'),
                    name: "TypeOfWork",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('LocatorEndDate'),
                    name: "LocatorEndDate",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('InitialStatusDate'),
                    name: "InitialStatusDate",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('LegalDuedate'),
                    name: "LegalDuedate",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('TicketStartDate'),
                    name: "TicketStartDate",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('UtilityName'),
                    name: "UtilityName",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "NumericField",
                    label: this.$t('FeetLocated'),
                    name: "FeetLocated",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true,
                    onKeyUp: this.calculateNetTotal
                },
                {
                    astype: "NumericField",
                    label: this.$t('MinutesWorked'),
                    name: "MinutesWorked",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('Emergency'),
                    name: "Emergency",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "SlotField",
                    label: this.$t('TicketImage'),
                    name: "TicketImage",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M'
                },
                {
                    astype: "TextAreaField",
                    label: this.$t('Comment'),
                    name: "Comment",
                    value: "",
                    config: {
                        rows: 3
                    }
                },
                {
                    astype: "SelectField",
                    label: this.$t('Utlityid'),
                    name: "UtilityId",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    config: {
                        options: [],
                        onChange: this.onSelectionUtilityChange
                    },
                    validationRules: {
                        required: true
                    },
                    disabled: this.IsTicketMannual === 'M'
                },
                {
                    astype: "SelectField",
                    label: this.$t('sba_id'),
                    name: "SbaId",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    config: {
                        options: []
                    },
                    validationRules: {
                        required: false
                    },
                    disabled: this.IsTicketMannual === 'M'
                },
                // Billing fields
                {
                    astype: "SelectField",
                    label: this.$t('CostCenters'),
                    name: "DistrictCode",
                    value: "",
                    visibility: true,
                    config: {
                        options: []
                    },
                    validationRules: {
                        required: true
                    }
                },
                {
                    astype: "SelectField",
                    label: this.$t('WorkCode'),
                    name: "WorkCodeId",
                    value: "",
                    visibility: true,
                    config: {
                        options: [],
                        onChange: this.onWorkCodeChange
                    },
                    validationRules: {
                        required: false
                    },
                },
                {
                    astype: "SelectField",
                    label: this.$t('Division'),
                    name: "DivisionId",
                    value: "",
                    visibility: true,
                    config: {
                        options: []
                    },
                    validationRules: {
                        required: true
                    },
                    disabled: this.IsTicketMannual === 'M'
                },
                // Manual fields (shown when Manual selected)
                {
                    astype: "NumericField",
                    label: this.$t('UnitRate'),
                    name: "UnitRate",
                    value: "",
                    visibility: false,
                    validationRules: {
                        required: true
                    },
                    onKeyUp: this.calculateNetTotalManual
                },
                {
                    astype: "NumericField",
                    label: this.$t('UnitQuantity'),
                    name: "UnitQuantity",
                    value: "",
                    visibility: false,
                    validationRules: {
                        required: true
                    },
                    onKeyUp: this.calculateNetTotalManual
                },
                {
                    astype: "TextField",
                    label: this.$t('NetTotal'),
                    name: "NetTotalManual",
                    value: "0.00",
                    visibility: false,
                    disabled: false
                },
                // Rate field (shown when editing existing item)
                {
                    astype: "NumericField",
                    label: this.$t('Rate'),
                    name: "UnitAmountTotal",
                    value: "",
                    visibility: this.columnId != 0 && this.columnId != null,
                    onKeyUp: this.calculateNetTotal
                },
                {
                    astype: "TextField",
                    label: this.$t('TermCode'),
                    name: "TermCode",
                    value: "",
                    visibility: true
                },
                {
                    astype: "SelectField",
                    label: this.$t('AdderName'),
                    name: "AdderNameLineItem",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    config: {
                        options: []
                    },
                    disabled: this.IsTicketMannual === 'M' || this.AdderUnit <= 0,
                    onChange: this.onAdderNameChange
                },
                {
                    astype: "NumericField",
                    label: this.$t('AdderUnitCost'),
                    name: "AdderUnitCost",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: this.IsTicketMannual === 'M' || this.AdderUnit <= 0,
                    onKeyUp: this.calculateNetTotal
                },
                {
                    astype: "TextField",
                    label: this.$t('AdderUnit'),
                    name: "AdderUnit",
                    value: "",
                    visibility: this.IsTicketMannual !== 'M',
                    disabled: true
                },
                {
                    astype: "TextField",
                    label: this.$t('NetTotal'),
                    name: "NetTotal",
                    value: "",
                    visibility: true,
                    disabled: false
                }
                ],
            },],
            buttons: [{
                type: "submit",
                class: "btn btn-info",
                text: "<i class='fa fa-plus pr-2'></i>Save & New",
                visibility: true
            },
            {
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save And Close",
            },
            {
                type: "submit",
                class: "btn btn-primary",
                text: "<i class='fa fa-save pr-2'></i>Save And Reprocess",
                visibility: true
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.Close,
            },
            ],

        }
    },
    created: async function () {
        var vm = this;
        await vm.GetStatusCodes();
        await vm.GetTicketTypes();
        await vm.GetWorkCodeData();
        await vm.GetCostCenters();
        await vm.fetchclientlist();
        if (vm.clientId) {
            await vm.GetDivisionDropdownData();
            await vm.LoadAdditionalInfoByClientId();
        }
        if (vm.lineItemId && vm.columnId) {
            // Hide radio buttons in edit mode
            await vm.$nextTick();
            if (vm.$refs.dynamicform) {
                vm.$refs.dynamicform.updateProperty("customlineitem", "visibility", false);
                // Set field states for edit mode
                vm.$refs.dynamicform.updateProperty("TicketNumber", "disabled", true);
                vm.$refs.dynamicform.updateProperty("ActivityId", "disabled", true);
                vm.$refs.dynamicform.UpdateKeyValue();
            }
            await vm.GetInvoiceLineItem();
        } else {
            // For new items, load ActivityId dropdown if ticket number is available
            await vm.$nextTick();
            if (vm.billId) {
                // Set TicketNumber if billId is provided
                vm.$refs.dynamicform.updateProperty("TicketNumber", "value", vm.billId);
                await vm.GetActivityIdDropdownListByTicketNumber();
            }
            // Initialize field visibility for add mode
            await vm.$nextTick();
            vm.ShowInvoiceFieldData(vm.IsTicketMannual, vm.ticketType);
        }
        
        // Update button visibility
        await vm.$nextTick();
        vm.updateButtonVisibility();

    },
    mounted: function() {
        var vm = this;
        // Ensure onChange handlers are properly bound after component is mounted
        vm.$nextTick(() => {
            if (vm.$refs.dynamicform) {
                // Re-bind onChange for UtilityId if needed
                var utilityField = vm.FormSchema[0].Data.find(f => f.name === "UtilityId");
                if (utilityField && utilityField.config) {
                    utilityField.config.onChange = vm.onSelectionUtilityChange;
                    vm.$refs.dynamicform.UpdateKeyValue();
                }
            }
        });
    },
    watch: {
        UtilityId: {
            handler: function(newVal, oldVal) {
                // Only trigger if value actually changed (not initial load)
                if (newVal && newVal !== oldVal) {
                    this.GetSbaIdListByUtilityId(newVal);
                }
            },
            immediate: false
        }
    },
    methods: {
        Close: function () {
            this.$emit('close');
        },
        GetStatusCodes: async function () {
            var vm = this;
            let selctedValue = [];
            locateBillingService.GetStatusCodes().then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.TEXT,
                                value: item.VALUE
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("StatusCode", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
        GetTicketTypes: async function () {
            var vm = this;
            let selctedValue = [];
            locateBillingService.GetTicketTypes().then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.TYPE_NAME,
                                value: item.TYPE_ID
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("TicketType", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
        GetActivityIdDropdownListByTicketNumber: async function () {
            var vm = this;
            let selctedValue = [];
            var ticketNumber = '';
            if (vm.$refs.dynamicform) {
                ticketNumber = vm.$refs.dynamicform.getPropertyValue("TicketNumber", "value") || '';
            }
            if (!ticketNumber && vm.billId) {
                ticketNumber = vm.billId;
                // Set TicketNumber in form if it's not set
                if (vm.$refs.dynamicform) {
                    vm.$refs.dynamicform.updateProperty("TicketNumber", "value", ticketNumber);
                }
            }
            if (!ticketNumber || !vm.clientId) {
                return;
            }
            var url = `ClientId=${vm.clientId}&ticketNumber=${ticketNumber}`;
            locateBillingService.GetActivityIdDropdownListByTicketNumber(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                value: item.ACTIVITY_ID
                            };
                            selctedValue.push(obj);
                        });
                        if (vm.$refs.dynamicform) {
                            vm.$refs.dynamicform.updateProperty("ActivityId", "config.options", selctedValue);
                            // Set the value if ActivityId is already known
                            if (vm.ActivityId) {
                                vm.$refs.dynamicform.updateProperty("ActivityId", "value", vm.ActivityId);
                            }
                            vm.$refs.dynamicform.UpdateKeyValue();
                        }
                    }
                }
            });
        },
        GetWorkCodeData: async function () {
            var vm = this;
            locateBillingService.GetWorkCodeData().then(response => {
                if (response.data) {
                    var options = response.data;
                    vm.$refs.dynamicform.updateProperty("WorkCodeId", "config.options", options);
                    vm.$refs.dynamicform.updateProperty("AdderNameLineItem", "config.options", options);
                    vm.$refs.dynamicform.UpdateKeyValue();
                }
            });
        },
        GetDivisionDropdownData: async function () {
            var vm = this;
            var url = `ClientId=${vm.clientId || vm.ClientId}`;
            if (!vm.clientId && !vm.ClientId) {
                return;
            }
            locateBillingService.GetDivisionDropdownData(url).then(response => {
                if (response.data) {
                    var options = response.data.map(item => ({
                        name: item.NAME,
                        value: item.DIVISIONS_ID
                    }));
                    vm.$refs.dynamicform.updateProperty("DivisionId", "config.options", options);
                    vm.$refs.dynamicform.UpdateKeyValue();
                }
            });
        },
        LoadAdditionalInfoByClientId: async function () {            
            var vm = this;
            let selctedValue = [];
            var url = `ClientId=${vm.clientId}`;
            locateBillingService.LoadAdditionalInfoByClientId(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.UTILITY_NAME,
                                value: item.UTILITY_ID
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("UtilityId", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
        GetSbaIdListByUtilityId: async function (utilityId) {
            debugger
            var vm = this;
            return new Promise((resolve, reject) => {
            let selctedValue = [];
                var url = `UtilityId=${utilityId || vm.UtilityId}`;
                if (!utilityId && !vm.UtilityId) {
                    resolve([]);
                    return;
                }
            locateBillingService.GetSbaIdListByUtilityId(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                    value: item.VALUE || item.SBA_ID
                            };
                            selctedValue.push(obj);
                        });
                            vm.$refs.dynamicform.updateProperty("SbaId", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                        }
                    }
                    resolve(selctedValue);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        onTicketNumberChange: function (value, x) {
            var vm = this;
            var ticketNumber = value;
            if (x && x.value) {
                ticketNumber = x.value;
            } else if (typeof value === 'object' && value.value) {
                ticketNumber = value.value;
            }
            
            if (!ticketNumber || !vm.clientId) {
                return;
            }
            
            // Load ActivityId dropdown when TicketNumber changes
            vm.GetActivityIdDropdownListByTicketNumber();
        },
        onActivityIdChange: function (value, x) {
            var vm = this;
            var activityId = value;
            if (x && x.value) {
                activityId = x.value;
            } else if (typeof value === 'object' && value.value) {
                activityId = value.value;
            }
            
            if (!activityId || !vm.clientId) {
                return;
            }
            
            var ticketNumber = vm.$refs.dynamicform ? vm.$refs.dynamicform.getPropertyValue("TicketNumber", "value") : '';
            if (!ticketNumber && vm.billId) {
                ticketNumber = vm.billId;
            }
            
            if (!ticketNumber) {
                return;
            }
            
            vm.isLoading = true;
            var url = `ticketNumber=${ticketNumber}&activityId=${activityId}`;
            locateBillingService.GetColumnId(url).then(response => {
                vm.isLoading = false;
                if (response.data) {
                    var columnId = response.data.columnId || 0;
                    var lineItemId = response.data.lineItemId || 0;
                    
                    if (columnId > 0) {
                        // Update props and reload the form
                        vm.columnId = columnId;
                        vm.lineItemId = lineItemId;
                        vm.isAdd = false;
                        // Hide radio buttons when editing
                        if (vm.$refs.dynamicform) {
                            vm.$refs.dynamicform.updateProperty("customlineitem", "visibility", false);
                            vm.$refs.dynamicform.updateProperty("TicketNumber", "disabled", true);
                            vm.$refs.dynamicform.updateProperty("ActivityId", "disabled", true);
                            vm.$refs.dynamicform.UpdateKeyValue();
                        }
                        // Reload the form with the found columnId and lineItemId
                        vm.GetInvoiceLineItem();
                    } else {
                        // No record found - show alert
                        vm.ShowAlert(vm.$t('NoRecordFoundForTicketNumber'), "failure", true, vm.$t("Alert"));
                    }
                }
            }).catch(error => {
                vm.isLoading = false;
                console.error('Error getting column ID:', error);
            });
        },
        onSelectionUtilityChange: function (value, x) {
            var vm = this;
            // Handle both single value and object parameter formats
            var utilityId = value;
            if (x && x.value) {
                utilityId = x.value;
            } else if (typeof value === 'object' && value.value) {
                utilityId = value.value;
            }
            vm.UtilityId = utilityId;
            if (utilityId) {
                vm.GetSbaIdListByUtilityId(utilityId).then(() => {
                    vm.$refs.dynamicform.UpdateKeyValue();
                });
            } else {
                vm.$refs.dynamicform.updateProperty("SbaId", "config.options", []);
                vm.$refs.dynamicform.updateProperty("SbaId", "value", "");
                vm.$refs.dynamicform.UpdateKeyValue();
            }
        },
        GetInvoiceLineItem: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `lineBatchId=${vm.lineBatchId || ''}&billID=0&lineItemId=${vm.lineItemId}&columnId=${vm.columnId}&ticketType=${vm.ticketType || ''}&isTicketMannual=${vm.isTicketManual || ''}`;
            locateBillingService.GetInvoiceLineItem(url).then(response => {
                vm.isLoading = false;
                if (response.data && response.data.length > 0) {
                    var item = response.data[0];
                    vm.UtilityId = item.CLIENT_ADDITIONAL_ID || '';
                    vm.ActivityId = item.ACTIVITY_ID || '';
                    vm.SbaId = item.BE_SBA_ID || item.SBA_ID || '';
                    
                    // Set TicketNumber first - this is critical
                    var ticketNumber = item.TICKETNUMBER || vm.billId || '';
                    vm.$refs.dynamicform.updateProperty("TicketNumber", "value", ticketNumber);
                    vm.$refs.dynamicform.updateProperty("ActivityId", "value", item.ACTIVITY_ID || '');
                    vm.$refs.dynamicform.updateProperty("Description", "value", item.DESCRIPTION || '');
                    vm.$refs.dynamicform.updateProperty("StatusCode", "value", item.STATUS || '');
                    vm.$refs.dynamicform.updateProperty("ClientId", "value", item.CLIENT_ID || '');
                    vm.$refs.dynamicform.updateProperty("TicketId", "value", item.TICKET_ID || '');
                    vm.$refs.dynamicform.updateProperty("LocatorName", "value", item.LOCATOR_NAME || '');
                    vm.$refs.dynamicform.updateProperty("StreetName", "value", item.STREETNAME || '');
                    vm.$refs.dynamicform.updateProperty("FeedName", "value", item.FEEDNAME || '');
                    vm.$refs.dynamicform.updateProperty("TransmissionId", "value", item.TRANSMISSION_ID || '');
                    vm.$refs.dynamicform.updateProperty("TicketType", "value", item.TICKETTYPE || '');
                    vm.$refs.dynamicform.updateProperty("WorkArea", "value", item.WORK_AREA || '');
                    vm.$refs.dynamicform.updateProperty("CallDate", "value", item.CALL_DATE || '');
                    vm.$refs.dynamicform.updateProperty("TransmitDate", "value", item.TRANSMIT_DATE || '');
                    vm.$refs.dynamicform.updateProperty("LocatorStartDate", "value", item.WORK_DATE || '');
                    vm.$refs.dynamicform.updateProperty("ParseDate", "value", item.PARSE_DATE || '');
                    vm.$refs.dynamicform.updateProperty("County", "value", item.COUNTY || '');
                    vm.$refs.dynamicform.updateProperty("State", "value", item.STATE || '');
                    vm.$refs.dynamicform.updateProperty("CallerName", "value", item.CALLER_NAME || '');
                    vm.$refs.dynamicform.updateProperty("CallerPhone", "value", item.CALLER_PHONE || '');
                    vm.$refs.dynamicform.updateProperty("WorkDoneFor", "value", item.WORK_DONE_FOR || '');
                    vm.$refs.dynamicform.updateProperty("Excavator", "value", item.EXCAVATOR || '');
                    vm.$refs.dynamicform.updateProperty("TypeOfWork", "value", item.TYPEOFWORK || '');
                    vm.$refs.dynamicform.updateProperty("LocatorEndDate", "value", item.CLOSED_DATE || '');
                    vm.$refs.dynamicform.updateProperty("InitialStatusDate", "value", item.INITIAL_STATUS_DATE || '');
                    vm.$refs.dynamicform.updateProperty("LegalDuedate", "value", item.LEGAL_DUE_DATE || '');
                    vm.$refs.dynamicform.updateProperty("TicketStartDate", "value", item.OUR_DUE_DATE || '');
                    vm.$refs.dynamicform.updateProperty("UtilityName", "value", item.UTILITY_NAME || '');
                    vm.$refs.dynamicform.updateProperty("FeetLocated", "value", item.FEET_LOCATED || '');
                    vm.$refs.dynamicform.updateProperty("MinutesWorked", "value", item.MINUTES_WORKED || '');
                    vm.$refs.dynamicform.updateProperty("Emergency", "value", item.EMERGENCY || '');
                    vm.$refs.dynamicform.updateProperty("Comment", "value", item.REMARKS || '');
                    
                    // Set UtilityId first, then load SBA options
                    vm.UtilityId = item.CLIENT_ADDITIONAL_ID || '';
                    vm.$refs.dynamicform.updateProperty("UtilityId", "value", item.CLIENT_ADDITIONAL_ID || '');
                    vm.$nextTick(() => {
                        if (item.CLIENT_ADDITIONAL_ID) {
                            vm.GetSbaIdListByUtilityId(item.CLIENT_ADDITIONAL_ID).then(() => {
                                // Set SBA Id after options are loaded
                                vm.SbaId = item.BE_SBA_ID || item.SBA_ID || '';
                                vm.$refs.dynamicform.updateProperty("SbaId", "value", item.BE_SBA_ID || item.SBA_ID || '');
                                vm.$refs.dynamicform.UpdateKeyValue();
                            });
                        }
                    });
                    
                    vm.$refs.dynamicform.updateProperty("DistrictCode", "value", item.DISTRICT_CODE || '');
                    vm.$refs.dynamicform.updateProperty("WorkCodeId", "value", item.WORK_CODE_ID || '');
                    vm.$refs.dynamicform.updateProperty("DivisionId", "value", item.DIVISION_ID || '');
                    vm.$refs.dynamicform.updateProperty("UnitRate", "value", item.UNIT_RATE || '');
                    vm.$refs.dynamicform.updateProperty("UnitQuantity", "value", item.UNIT_WORKED || '');
                    vm.$refs.dynamicform.updateProperty("UnitAmountTotal", "value", item.UNIT_TOTAL_AMOUNT || '');
                    vm.$refs.dynamicform.updateProperty("TermCode", "value", item.TERM_CODE || '');
                    vm.$refs.dynamicform.updateProperty("AdderNameLineItem", "value", item.ADDER_NAME || '');
                    vm.$refs.dynamicform.updateProperty("AdderUnitCost", "value", item.ADDER_UNIT_COST || '');
                    vm.AdderUnit = parseFloat(item.ADDER_UNIT || 0);
                    vm.$refs.dynamicform.updateProperty("AdderUnit", "value", item.ADDER_UNIT || '');
                    vm.$refs.dynamicform.updateProperty("NetTotal", "value", item.AMOUNT || item.UNIT_TOTAL_AMOUNT || '');
                    
                    // Set IsTicketMannual based on data
                    vm.IsTicketMannual = item.IS_TICKET_MANUAL || 'T';
                    vm.ticketType = item.TICKETTYPE || vm.ticketType;
                    
                    // Apply field visibility and disabled states
                    vm.$nextTick(() => {
                        vm.ShowInvoiceFieldData(vm.IsTicketMannual, item.TICKETTYPE);
                        vm.updateButtonVisibility();
                    });
                    
                    vm.$refs.dynamicform.UpdateKeyValue();
                    
                    // Store old values for change tracking
                    vm.oldValues = {
                        TicketNumber: item.TICKETNUMBER || '',
                        ActivityId: item.ACTIVITY_ID || '',
                        Description: item.DESCRIPTION || '',
                        Status: item.STATUS || '',
                        ClientId: item.CLIENT_ID || '',
                        TicketId: item.TICKET_ID || '',
                        LocatorName: item.LOCATOR_NAME || '',
                        StreetName: item.STREETNAME || '',
                        FeedName: item.FEEDNAME || '',
                        TransmissionId: item.TRANSMISSION_ID || '',
                        TicketType: item.TICKETTYPE || '',
                        WorkArea: item.WORK_AREA || '',
                        UtilityId: item.CLIENT_ADDITIONAL_ID || '',
                        SbaId: item.BE_SBA_ID || item.SBA_ID || '',
                        DistrictCode: item.DISTRICT_CODE || '',
                        WorkCodeId: item.WORK_CODE_ID || '',
                        DivisionId: item.DIVISION_ID || '',
                        UnitRate: item.UNIT_RATE || '',
                        UnitQuantity: item.UNIT_WORKED || '',
                        UnitAmountTotal: item.UNIT_TOTAL_AMOUNT || '',
                        TermCode: item.TERM_CODE || '',
                        AdderNameLineItem: item.ADDER_NAME || '',
                        AdderUnitCost: item.ADDER_UNIT_COST || '',
                        AdderUnit: item.ADDER_UNIT || '',
                        Comment: item.REMARKS || ''
                    };
                }
            }).catch(error => {
                vm.isLoading = false;
                console.error('Error loading invoice line item:', error);
            });
        },
        GetCostCenters: async function () {
            var vm = this;
            let selctedValue = [];
            locateBillingService.GetCostCenters().then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.TEXT,
                                value: item.VALUE
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("DistrictCode", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
        fetchclientlist: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `reqFrom=CUSTOM`;
            locateBillingService.getClientList(url).then(response => {
                vm.isLoading = false;
                vm.clientList = response.data.items;
                vm.$refs.dynamicform.updateProperty("ClientId", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
            }).catch(error => {
                vm.isLoading = false;
                vm.clientList = [];
                vm.$refs.dynamicform.updateProperty("ClientId", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
                console.error('Error fetching client list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading = true;
            vm.UpdateChangedValues(formfields);
            var ProcessLogJson = JSON.stringify(vm.MakeJsonForProcessLog(formfields));
            let text = val.replace(/<[^>]*>/g, '').trim();
            let isSaveClicked = text === 'Save And Close' || text === 'Save & New' ? 1 : 0;
            let buttonName = text === 'Save & New' ? 'saveandnew' : '';
            
            // Convert dateOfInvoice to string if it's a Date object
            var dateOfInvoiceStr = '';
            if (vm.dateOfInvoice) {
                if (vm.dateOfInvoice instanceof Date) {
                    dateOfInvoiceStr = vm.dateOfInvoice.toISOString().split('T')[0];
                } else {
                    dateOfInvoiceStr = vm.dateOfInvoice.toString();
                }
            }
            
            var postData = {
                columnId: vm.columnId,
                SetupId: "",
                BatchId: vm.batchId || "",
                BillId: vm.billId || 0,
                IsAdd: vm.isAdd,
                IsReadOnlyForReport: "",
                IsTicketMannual: vm.isTicketManual,
                IsTicketFlatFee: vm.ticketType,
                LineItemId: vm.lineItemId,
                DateOfInvoiceLine: dateOfInvoiceStr,
                InvoiceBatchId: vm.batchId || "",
                RecordOnSearchTicketNumer: "yes",
                TicketNumber: formfields.TicketNumber,
                ActivityId: formfields.ActivityId,
                Description: formfields.Description,
                Status: formfields.StatusCode || formfields.Status,
                ClientId: formfields.ClientId,
                TicketId: formfields.TicketId,
                LocatorName: formfields.LocatorName,
                StreetName: formfields.StreetName,
                FeedName: formfields.FeedName,
                TransmissionId: formfields.TransmissionId,
                TicketType: formfields.TicketType,
                WorkArea: formfields.WorkArea,
                CallDate: formfields.CallDate,
                TransmitDate: formfields.TransmitDate,
                WorkDate: formfields.LocatorStartDate,
                ParseDate: formfields.ParseDate,
                County: formfields.County,
                State: formfields.State,
                CallerName: formfields.CallerName,
                CallerPhone: formfields.CallerPhone,
                WorkDoneFor: formfields.WorkDoneFor,
                Excavator: formfields.Excavator,
                TypeOfWork: formfields.TypeOfWork,
                ClosedDate: formfields.LocatorEndDate,
                InitialStatusDate: formfields.InitialStatusDate,
                LegalDuedate: formfields.LegalDuedate,
                OurDueDate: formfields.TicketStartDate,
                UtilityName: formfields.UtilityName,
                FeetLocated: formfields.FeetLocated,
                MinutesWorked: formfields.MinutesWorked,
                Emergency: formfields.Emergency,
                Comment: formfields.Comment,
                ClientAdditionalId: formfields.UtilityId,
                SbaId: formfields.SbaId,
                DistrictCode: formfields.DistrictCode,
                WorkCodeId: formfields.WorkCodeId,
                DivisionId: formfields.DivisionId,
                UnitRate: formfields.UnitRate,
                UnitQuantity: formfields.UnitQuantity,
                UnitAmountTotal: formfields.UnitAmountTotal,
                TermCode: formfields.TermCode,
                AdderNameLineItem: formfields.AdderNameLineItem,
                AdderUnitCost: formfields.AdderUnitCost,
                AdderUnit: formfields.AdderUnit,
                NetTotal: formfields.NetTotal,
                ProcessLogJson: ProcessLogJson,
                newStringifyModel: JSON.stringify(formfields),
                oldStringifyModel: JSON.stringify(vm.oldValues || {}),
                isSaveClicked: isSaveClicked
            };

            locateBillingService.UpdateInvoiceLineItem(postData).then(response => {
                vm.isLoading = false;
                if (response.data && response.data.msg === "Success") {
                    vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                    if (isSaveClicked == 1 || isSaveClicked == 0) {
                        vm.$emit('saved', { buttonName: buttonName });
                        if (buttonName !== 'saveandnew') {
                            vm.Close();
                        } else {
                            // Reset form for new entry
                            vm.$refs.dynamicform.resetForm();
                            vm.oldValues = {};
                        }
                    }
                } else if (response.data && response.data.msgFor === "invalid") {
                    if (response.data.invalidKey) {
                        response.data.invalidKey.forEach(function (ctr) {
                            vm.$refs.dynamicform.showError(ctr.ctrId, ctr.ctrMsg);
                        });
                    }
                    vm.ShowAlert(response.data.msg, "failure", true, vm.$t("Alert"));
                } else {
                    vm.ShowAlert(response.data.msg || vm.$t("ErrorWhileProcessingRequest"), response.data.msgFor || "failure", true, vm.$t("Alert"));
                }
            }).catch(() => {
                vm.isLoading = false;
                vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t("Alert"));
            });
        },
        UpdateChangedValues(formfields) {
            var vm = this;
            var data = "{";
            for (var key in formfields) {
                if (formfields[key] !== (vm.oldValues && vm.oldValues[key])) {
                    data += '"' + key + '":' + JSON.stringify(formfields[key]) + ",";
                }
            }
            if (data === "{") {
                vm.$refs.dynamicform.updateProperty("Comment", "value", "");
            } else {
                data = data.substr(0, data.length - 1) + "}";
                vm.$refs.dynamicform.updateProperty("Comment", "value", data);
            }
        },
        MakeJsonForProcessLog(formfields) {
            var vm = this;
            var dataArray = [];
            for (var key in formfields) {
                if (formfields[key] !== (vm.oldValues && vm.oldValues[key]) && key !== "Comment") {
                    dataArray.push({
                        ControlName: key,
                        OldData: (vm.oldValues && vm.oldValues[key]) || "",
                        NewData: formfields[key] || ""
                    });
                }
            }
            return dataArray;
        },
        onWorkCodeChange: function (value) {
            // Handle work code change if needed
        },
        onAdderNameChange: function (value) {
            // Handle adder name change if needed
        },
        onCustomLineItemChange: function (value) {
            var vm = this;
            vm.IsTicketMannual = value;
            vm.ShowInvoiceFieldData(value);
        },
        updateButtonVisibility: function() {
            var vm = this;
            var isManual = vm.IsTicketMannual === 'M' || vm.ticketType === 'F';
            var saveNewBtn = vm.buttons.find(b => b.text && b.text.includes('Save & New'));
            var saveReprocessBtn = vm.buttons.find(b => b.text && b.text.includes('Save And Reprocess'));
            
            if (saveNewBtn) {
                saveNewBtn.visibility = !isManual && vm.billId > 0 && (vm.columnId == 0 || vm.columnId == null);
            }
            if (saveReprocessBtn) {
                saveReprocessBtn.visibility = !isManual;
            }
            
            // Update form buttons if dynamicform supports it
            if (vm.$refs.dynamicform && vm.$refs.dynamicform.updateButtons) {
                vm.$refs.dynamicform.updateButtons(vm.buttons);
            }
        },
        ShowInvoiceFieldData: function (val, ticketType) {
            var vm = this;
            var isManual = val === 'M' || ticketType === 'F';
            
            // Update button visibility
            vm.updateButtonVisibility();
            
            if (isManual) {
                // Show manual fields, hide ticket fields
                vm.$refs.dynamicform.updateProperty("UnitRate", "visibility", true);
                vm.$refs.dynamicform.updateProperty("UnitQuantity", "visibility", true);
                vm.$refs.dynamicform.updateProperty("NetTotalManual", "visibility", true);
                
                // Hide ticket-specific fields
                var ticketFields = ["Description", "TicketId", "LocatorName", "StreetName", "FeedName", 
                    "TransmissionId", "TicketType", "WorkArea", "CallDate", "TransmitDate", "LocatorStartDate",
                    "ParseDate", "County", "State", "CallerName", "CallerPhone", "WorkDoneFor", "Excavator",
                    "TypeOfWork", "LocatorEndDate", "InitialStatusDate", "LegalDuedate", "TicketStartDate",
                    "UtilityName", "FeetLocated", "MinutesWorked", "Emergency", "TicketImage", "UtilityId", "SbaId"];
                ticketFields.forEach(field => {
                    vm.$refs.dynamicform.updateProperty(field, "visibility", false);
                });
                
                // Disable fields
                vm.$refs.dynamicform.updateProperty("TicketNumber", "disabled", true);
                vm.$refs.dynamicform.updateProperty("ActivityId", "disabled", true);
                vm.$refs.dynamicform.updateProperty("StatusCode", "disabled", true);
                vm.$refs.dynamicform.updateProperty("TicketType", "disabled", true);
                vm.$refs.dynamicform.updateProperty("UtilityId", "disabled", true);
                vm.$refs.dynamicform.updateProperty("SbaId", "disabled", true);
                vm.$refs.dynamicform.updateProperty("DivisionId", "disabled", true);
                vm.$refs.dynamicform.updateProperty("WorkCodeId", "disabled", ticketType === 'F');
                vm.$refs.dynamicform.updateProperty("AdderNameLineItem", "disabled", true);
                vm.$refs.dynamicform.updateProperty("AdderUnitCost", "disabled", true);
                
                if (ticketType === 'F') {
                    vm.$refs.dynamicform.updateProperty("UnitQuantity", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("NetTotalManual", "visibility", false);
                }
            } else {
                // Show ticket fields, hide manual fields
                vm.$refs.dynamicform.updateProperty("UnitRate", "visibility", false);
                vm.$refs.dynamicform.updateProperty("UnitQuantity", "visibility", false);
                vm.$refs.dynamicform.updateProperty("NetTotalManual", "visibility", false);
                
                // Show ticket-specific fields
                var ticketFields = ["Description", "TicketId", "LocatorName", "StreetName", "FeedName", 
                    "TransmissionId", "TicketType", "WorkArea", "CallDate", "TransmitDate", "LocatorStartDate",
                    "ParseDate", "County", "State", "CallerName", "CallerPhone", "WorkDoneFor", "Excavator",
                    "TypeOfWork", "LocatorEndDate", "InitialStatusDate", "LegalDuedate", "TicketStartDate",
                    "UtilityName", "FeetLocated", "MinutesWorked", "Emergency", "TicketImage", "UtilityId", "SbaId"];
                ticketFields.forEach(field => {
                    vm.$refs.dynamicform.updateProperty(field, "visibility", true);
                });
                
                // Enable/disable based on edit mode
                if (vm.columnId > 0) {
                    // Edit mode: TicketNumber and ActivityId are disabled
                    vm.$refs.dynamicform.updateProperty("TicketNumber", "disabled", true);
                    vm.$refs.dynamicform.updateProperty("ActivityId", "disabled", true);
                    vm.$refs.dynamicform.updateProperty("StatusCode", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("TicketType", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("UtilityId", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("SbaId", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("DivisionId", "disabled", false);
                } else {
                    // Add mode: All enabled
                    vm.$refs.dynamicform.updateProperty("TicketNumber", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("ActivityId", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("StatusCode", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("TicketType", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("UtilityId", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("SbaId", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("DivisionId", "disabled", false);
                }
                
                // Enable adder fields if AdderUnit > 0
                if (vm.AdderUnit > 0) {
                    vm.$refs.dynamicform.updateProperty("AdderNameLineItem", "disabled", false);
                    vm.$refs.dynamicform.updateProperty("AdderUnitCost", "disabled", false);
                } else {
                    vm.$refs.dynamicform.updateProperty("AdderNameLineItem", "disabled", true);
                    vm.$refs.dynamicform.updateProperty("AdderUnitCost", "disabled", true);
                }
            }
            
            vm.$refs.dynamicform.UpdateKeyValue();
        },
        onSelectionChange: function (value, x) {
            var vm = this;
            if (x && x.length > 0) {
                var clientId = Array.isArray(x) ? x[0] : x;
                vm.clientId = clientId;
                vm.GetDivisionDropdownData();
                vm.LoadAdditionalInfoByClientId();
            }
        },
        calculateNetTotal: function () {
            // Calculate net total
        },
        calculateNetTotalManual: function () {
            // Calculate net total for manual items
        }
    }
}
</script>
